export default function reducer(state = {}, action) {
  switch (action.type) {
    case 'set-username':
      return {
        ...state,
        username: {
          text: action.username,
        },
      };

    case 'set-password':
      return {
        ...state,
        password: {
          text: action.text,
          isEncrypt: action.isEncrypt,
        },
      };

    default:
      return state;
  }
}
