export default function reducer(state = {}, action) {
  switch (action.type) {
    case 'set-new-password':
      return {
        ...state,
        newPassword: {
          text: action.text,
          strength: action.strength,
        },
        confirmPassword: {
          ...state.confirmPassword,
          isMatched: action.isMatched,
        },
      };

    case 'set-confirm-password':
      return {
        ...state,
        confirmPassword: {
          text: action.text,
          isEncrypt: action.isEncrypt,
          isMatched: action.isMatched,
        },
      };

    default:
      return state;
  }
}
