import { pages } from './pages';
import { Switch, Route, BrowserRouter as Router } from 'react-router-dom';

export default function Navigator() {
  return (
    <Router basename='/'>
      <Switch>
        {pages.map((page, index) => (
          <Route strict sensitive key={index} {...page} />
        ))}
      </Switch>
    </Router>
  );
}
