import styles from './.module.css';
import { View, Separator, Text } from 'components';
import { logoPage } from 'assets/icons';

export default function Loading() {
  return (
    <View style={styles.mainPane}>
      <img src={logoPage} className={styles.logo} alt='img-logo' />
      <Separator vertical={5} />
      <Text style={styles.title}>Welcome</Text>
      <Text style={styles.subtitle}>BroowingCoffee Server</Text>
    </View>
  );
}
