import styles from './.module.css';
import Icon from 'react-web-vector-icons';

import { Button, Separator, Text, TextInput, View } from 'components';
import { accentColor } from 'constants/styles';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router';

export default function Login() {
  const history = useHistory();
  const [email, setEmail] = useState();
  const [loading, setLoading] = useState();

  const onClick = (component) => {
    if (component === 'on-request') {
      setLoading(true);
      const timeout = setTimeout(() => {
        history.replace('/');
        setLoading(false);
        clearTimeout(timeout);
      }, 1000);
    }
  };
  const onChangeValue = (component, value) => {
    if (component === 'email') {
      setEmail(value);
    }
  };

  useEffect(() => {}, [history]);

  return (
    <View style={styles.mainPane}>
      <View style={styles.bodyPane}>
        <View style={styles.upperBodyPane}>
          <View style={styles.headerPane}>
            <Text style={styles.headerTitle}>Forgot Password</Text>
            <Text style={styles.headerSubtitle}>
              Please provide your email for renewal
            </Text>
          </View>
        </View>
        <Separator vertical={10} />
        <View style={styles.centerBodyPane}>
          <TextInput
            skin={styles.inputSkin}
            placeholder={'Email'}
            prefixIcon={<Icon name='mail' font='Feather' color={accentColor} size={17.5} />}
            value={email}
            onChangeText={(value) => onChangeValue('email', value)}
          />
          <Separator vertical={7.5} />
          <Button
            title={'Send'}
            skin={styles.buttonSkin}
            isLoading={loading}
            onClick={() => onClick('on-request')}
          />
        </View>
      </View>
    </View>
  );
}
