import styles from './.module.css';
import * as Progress from 'components/Progress';
import { backgroundColor } from 'constants/styles';
export default function Button({ children, title, skin, isLoading, ...props }) {
  return (
    <button {...props} className={`${styles.skin} ${skin}`} disabled={isLoading}>
      {!isLoading && <>{title || children}</>}
      {isLoading && <Progress.CircleSnail color={backgroundColor} size={20} />}
    </button>
  );
}
