import Icon from 'react-web-vector-icons';
import { useResetPassword } from 'hooks';
import styles from './.module.css';

import { Button, Separator, TextInput, View, Text } from 'components';
import { accentColor } from 'constants/styles';
import { SMALL_CHAR_REGEX, CAPITAL_CHAR_REGEX, NUMS_REGEX, SYMBOLS_REGEX } from 'constants/regex';
import { RESET_PASSWORD_FIELDS } from 'constants/string';

import { useEffect } from 'react';
import { useParams } from 'react-router';

export default function ResetPassword() {
  const [state, setState] = useResetPassword(RESET_PASSWORD_FIELDS);
  const onClick = (component) => {
    if (component === 'on-change') {
    } else if (component === 'on-encrypt-text') {
      setState({
        ...state.confirmPassword,
        type: 'set-confirm-password',
        isEncrypt: !state.confirmPassword.isEncrypt,
      });
    }
  };
  const onChangeValue = (component, value) => {
    if (component === 'new-password') {
      setState({
        ...state.newPassword,
        type: `set-${component}`,
        text: value,
        strength: value.length > 5 ? onStrength(value) : 0,
        isMatched: value.length > 5 ? value === state.confirmPassword.text : false,
      });
    } else if (component === 'confirm-password') {
      setState({
        ...state.confirmPassword,
        type: `set-${component}`,
        text: value,
        isMatched: value.length > 5 ? value === state.newPassword.text : false,
      });
    }
  };
  const onStrength = (value) => {
    let strength = 0;
    if (SMALL_CHAR_REGEX.test(value)) {
      strength += 25;
    }
    if (CAPITAL_CHAR_REGEX.test(value)) {
      strength += 25;
    }
    if (NUMS_REGEX.test(value)) {
      strength += 25;
    }
    if (SYMBOLS_REGEX.test(value)) {
      strength += 25;
    }
    return strength;
  };
  const onMatched = (status) => {
    if (status) {
      return <Icon name='check' font='Feather' color={accentColor} size={17.5} />;
    } else {
      return <Icon name='x' font='Feather' color={accentColor} size={17.5} />;
    }
  };

  const { token } = useParams();
  useEffect(() => {
    console.log('CLIENT:', token);
  }, [token]);

  return (
    <View style={styles.mainPane}>
      <View style={styles.bodyPane}>
        <View style={styles.upperBodyPane}>
          <View style={styles.headerPane}>
            <Text style={styles.headerTitle}>Reset Password</Text>
            <Text style={styles.headerSubtitle}>Please enter your new password</Text>
          </View>
        </View>
        <Separator vertical={15} />
        <View style={styles.centerBodyPane}>
          <TextInput
            skin={styles.inputSkin}
            placeholder={'New Password'}
            indicatorProgress={state.newPassword.strength}
            indicatorColor={accentColor}
            isTextEncrypt={!state.confirmPassword.isEncrypt}
            prefixIcon={<Icon name='lock' font='Feather' color={accentColor} size={17.5} />}
            value={state.newPassword.text}
            onChangeText={(value) => onChangeValue('new-password', value)}
          />
          <Separator vertical={3} />
          <TextInput
            skin={styles.inputSkin}
            placeholder={'Confirm-Password'}
            isTextEncrypt={!state.confirmPassword.isEncrypt}
            onEncryptText={() => onClick('on-encrypt-text')}
            prefixIcon={onMatched(state.confirmPassword.isMatched)}
            value={state.confirmPassword.text}
            onChangeText={(value) => onChangeValue('confirm-password', value)}
          />
          <Separator vertical={7.5} />
          <Button title={'Change'} skin={styles.buttonSkin} onClick={() => onClick('on-change')} />
          <Separator vertical={10} />
        </View>
      </View>
    </View>
  );
}
