import styles from './.module.css';
import Icon from 'react-web-vector-icons';

import { Button, Separator, Text, TextInput, View } from 'components';
import { logoProject } from 'assets/icons';
import { accentColor } from 'constants/styles';
import { LOGIN_FIELDS } from 'constants/string';
import { USERNAME_REGEX } from 'constants/regex';
import { useLogin } from 'hooks';
import { useEffect, useState } from 'react';
import { useHistory } from 'react-router';

export default function Login() {
  const history = useHistory();
  const [state, setState] = useLogin(LOGIN_FIELDS);
  const [loading, setLoading] = useState(false);

  const onClick = (component) => {
    if (component === 'on-login') {
      setLoading(true);
      const timeout = setTimeout(() => {
        history.push('/dashboard');
        clearTimeout(timeout);
        setLoading(false);
      }, 1000);
    } else if (component === 'on-forgot-password') {
      history.push('/forgotpassword');
    } else if (component === 'on-encrypt-text') {
      setState({
        ...state.password,
        type: 'set-password',
        isEncrypt: !state.password.isEncrypt,
      });
    }
  };
  const onChangeValue = (component, value) => {
    if (component === 'username') {
      if (USERNAME_REGEX.test(value) || value.length === 0) {
        setState({
          type: 'set-username',
          text: value,
        });
      }
    } else if (component === 'password') {
      setState({
        ...state.password,
        type: 'set-password',
        text: value,
      });
    }
  };

  useEffect(() => {});

  return (
    <View style={styles.mainPane}>
      <View style={styles.bodyPane}>
        <View style={styles.leftPane}>
          <img src={logoProject} className={styles.leftPaneLogo} alt={'logo'} />
          <Text style={styles.leftPaneTitle}>Welcome!</Text>
        </View>
        <View style={styles.rightPane}>
          <View style={styles.upperRightPane}>
            <Text style={styles.rightPaneTitle}>BroowingCoffee</Text>
          </View>
          <Separator vertical={15} />
          <View style={styles.centerRightPane}>
            <TextInput
              skin={styles.inputSkin}
              placeholder={'Username'}
              prefixIcon={<Icon name='user' font='Feather' color={accentColor} size={17.5} />}
              value={state.username.text}
              onChangeText={(value) => onChangeValue('username', value)}
            />
            <Separator vertical={3} />
            <TextInput
              skin={styles.inputSkin}
              placeholder={'Password'}
              isTextEncrypt={state.password.isEncrypt}
              onEncryptText={() => onClick('on-encrypt-text')}
              prefixIcon={<Icon name='lock' font='Feather' color={accentColor} size={17.5} />}
              value={state.password.text}
              onChangeText={(value) => onChangeValue('password', value)}
            />
            <Separator vertical={7.5} />
            <Button
              title={'Login'}
              skin={styles.buttonSkin}
              onClick={() => onClick('on-login')}
              isLoading={loading}
            />
            <Separator vertical={10} />
            <Button
              skin={styles.buttonForgotPassword}
              onClick={() => onClick('on-forgot-password')}
              title={'Forgot Password'}
            />
          </View>
        </View>
      </View>
    </View>
  );
}
